/**
 * A specific preference.

 */
export class Preference {
    /**
     * The unique id of the preference
     */
    id: string;
    /**
     * The value of the preference. Note that this can be of any JSON type.
     */
    value?: string;

    constructor(input?: any) {
        if (input) {
            Object.assign(this, input);
        }
    }

}
